          this.orderList = res.data;
          this.$emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
  },
  mounted() {
    this.getOrders();
    this.$emitter.on('getOrderLsit', () => {
      this.getOrders();
    });
  },
};
